$color-bg: rgb(226, 180, 89);
$color-bg-active: rgb(226, 180, 85);
$color-dark: #282828;
$color-light: #ffffff;
$font-light: 400;
$font-bold: 600;
$first-break: 700px;
$second-break: 1100px;

@mixin image_position {
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
};

@keyframes pend_animation {
    0% {
        transform: rotate(0) translate(-50%);
    }
    25%{
        transform: rotate(45deg) translate(-50%);
    }
    50% {
        transform: rotate(0) translate(-50%);
    }
    75%{
        transform: rotate(-45deg) translate(-50%);
    }
    100% {
        transform: rotate(0) translate(-50%);
    }
};

@media (-webkit-device-pixel-ratio: 2) {
$color-bg: rgb(226, 180, 89);
$color-bg-active: rgb(226, 180, 85);
$color-dark: #282828;
$color-light: #ffffff;
$font-light: 400;
$font-bold: 600;
